define(['jquery', 'popover'], function($) {
    return {
        init: function () {
            require(['mod_bootstrapelements/fontawesome-iconpicker'], function() {

                $(function() {
                    $("#id_bootstrapicon").iconpicker({placement: "right", selectedCustomClass: "label label-success"});
                });
            });
        }

    };
});
